@import url('https://fonts.googleapis.com/css2?family=Dancing+Script:wght@700&family=Pacifico&display=swap');

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(135deg, #f8c9d7 0%, #ec9eb5 100%);
  min-height: 100vh;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


/* Utility classes for mobile-friendly layout */
.container {
  max-width: 480px;
  margin: 0 auto;
  padding: 16px;
  background: #ffffff;
  border-radius: 24px;
  box-shadow: 0 4px 24px rgba(255, 182, 193, 0.10);
}

.title {
  color: #e75480;
  font-size: 2rem;
  font-weight: 700;
  text-align: center;
  margin-block-start: 0;
  margin-block-end: 0;
  margin-bottom: 16px;
}

.subtitle {
  color: #b23a48;
  font-size: 1.1rem;
  margin-bottom: 24px;
  text-align: center;
}

.status-box {
  background: #ffe4ec;
  border-radius: 12px;
  padding: 16px;
  color: #b23a48;
  font-weight: 500;
  font-size: 1.1rem;
  text-align: center;
}

@media (max-width: 600px) {
  .container {
    padding: 16px 4px;
    border-radius: 0;
    box-shadow: none;
    min-height: 100vh;
  }
  .title {
    font-size: 1.5rem;
  }
}

/* Feed wrapper */
.status-feed {
  display: flex;
  flex-direction: column;
  gap: 20px; /* consistent spacing between posts */
  margin-top: 24px;
}

/* Each post card */
.status-card {
  background: #fff;
  border: 1px solid #eee;
  border-radius: 16px;
  padding: 20px;
  box-shadow: 0px 4px 5px #9c9c9c;
  transition: transform 0.1s ease, box-shadow 0.1s ease;
}

/* Timestamp style */
.status-time {
  font-size: 1.25rem;
  color: #313131;
  margin-bottom: 10px;
  font-weight: 500;
}

/* Main text style */
.status-text {
  font-size: 1.2rem;
  line-height: 1.5;
  color: #333;
  word-wrap: break-word;
}

/* Mobile refinements */
@media (max-width: 600px) {
  .status-card {
    border-radius: 12px;
    padding: 16px;
  }
  .status-time {
    font-size: 0.75rem;
  }
  .status-text {
    font-size: 1rem;
  }
}
